      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" style="width: 100%" :max-height="tableHeight" v-loading="loading1" :summary-method="getSummaries" show-summary>
        <el-table-column label="序号" type="index" width="46" align="center"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="company_name" label="库存组织"></el-table-column>
