        <!-- 需求688和泽乾对接，后期没准加上先注释 -->
        <!-- <el-table-column :show-overflow-tooltip="true" property="occupy_num" width="138px;" label="订单占用库存" align="left"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="available_stocks" width="138px;" label="可用库存" align="left"></el-table-column> -->
