<script>
import {
  getData,
  getInventoryFinanceCompanyOpen,
  tableResize, //明细表去除页面滚动条
} from "common/utils/public_function/commonUtils";
export default {
  name: "batchsummary",
  data() {
    return {
      authority: {},
      params: {},
      searchParams: {},
      listShow: false,
      timeValue: [],
      exportType: null,
      warehouseList: {
        options: [],
      }, //仓库管理
      TableData: {
        allList: {},
        tableData: [],
      },
      departmentList: {
        options: [],
      },
      companyList: [],
      optionsW: null,
      getwarehouseOption: true, //二级联动货位
      loading: true,
      goPage: 1, //默认搜索页面
      pageSize: 30,
      per_page: [30, 50, 100, 500],
      loadingBrand: false,
      tableHeight: 400,
    };
  },

  methods: {
    companyChange(val) {
      this.$set(this.params, "warehouse_id", " ");
      this.$set(this.params, "goods_name", " ");
    },
    renderHeaderpro() {
      return (
        <el-tooltip class="item" content="Bottom center" effect="light">
          <p slot="content">生产日期列后期将会隐藏，同时新增商品入库日期列</p>
          <span
            class="el-icon-question"
            style="cursor:pointer;font-weight:bold;"
          >
            生产日期
          </span>
        </el-tooltip>
      );
    },
    change(val) {
      //选择的日期
      this.params.start_time = val[0];
      this.params.end_time = val[1];
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.$set(this, "params", {
        company_id: Number(localStorage.company_id),
      });
      this.timeValue = [];
      this.getwarehouseOption = true;
      this.$set(this, "searchParams", {
        params: {}, //1默认带的参数
        TableData: this.TableData, //列表数据
        API: "getBatchSummaryList", //接口名
        vm: this, //传递的this
      });
      Object.assign(this.searchParams.params, this.params, {
        page: 1,
      });
      getData(this.searchParams, 2, tableResize.bind(this));
    },
    exportOrder(scheduleType) {
      // 商品库存量导出
      if (this.listShow == false) {
        this.$message({
          message: "请查询需要导出的数据",
          type: "error",
        });
        return false;
      }
      let s = Object.assign(this.params, {
        export_type: scheduleType,
      });
      this.$axMethod.exportBatchSummaryUrl(this.params).then((response) => {
        if (response.data.code == 200) {
          window.location.href = response.data.data.url;
        }
      });
    },
    getSummaries(param) {
      let _this = this;
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "合计";
          return;
        }
        if (index === 1) {
          sums[index] = " ";
          return;
        }
        const values = data.map((item) => Number(item[column.property]));
        if (!values.every((value) => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index === 10) {
            sums[index] = sums[index];
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });
      return sums;
    },
    warehouseChangeId(val) {
      // 仓库ID的获取
      this.getwarehouseOption = false;
      this.$axMethod
        .allocationInventoryList({
          warehouse_id: val,
        })
        .then((response) => {
          if (response.data.code == 200) {
            this.optionsW = response.data.data;
            this.$set(this.params, "allocation_id", " ");
          }
        });
    },
    onSearch() {
      //查询
      this.$refs.filterFormRef.validate((valid) => {
        if (valid) {
          this.listShow = true;
          this.$set(this, "searchParams", {
            params: {}, //1默认带的参数
            TableData: this.TableData, //列表数据
            API: "getBatchSummaryList", //接口名
            vm: this, //传递的this
          });
          Object.assign(this.searchParams.params, this.params, {
            page: 1,
          });

          getData(this.searchParams, 2, tableResize.bind(this));
        }
      });
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1,
      });
      getData(this.searchParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val,
      });
      getData(this.searchParams);
    },
    companyFilter(val) {
      let options = {
        type: 1,
        company_name: val,
      };
      getInventoryFinanceCompanyOpen(this, options);
    },
    getCompany() {
      let options = {
        type: 1,
        company_name: null,
      };
      getInventoryFinanceCompanyOpen(this, options);
    },
    getWarehouse() {
      this.$common.options("getWarehouseListOption", this.warehouseList, {
        company_id: this.params.company_id,
      }); //仓库下拉
    },
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.$set(this, "params", {
      company_id: Number(localStorage.company_id),
    });
    this.companyList = [
      {
        company_id: Number(localStorage.company_id),
        company_name: localStorage.company_name,
      },
    ];
    //取消单据右侧滚动条
    tableResize.bind(this)();
    window.onresize = tableResize.bind(this);
  },
};
</script>

